.notes_list__note {
  font-size: 1.2em;
  padding: 1rem;
  /* border: 2px solid gray; */
  border-radius: 0.3rem;
  text-align: start;
  background: rgb(62, 77, 100);
  overflow: hidden;
  display: flex;
  align-items: center;
}

.notes_list__note__done {
  background: rgb(51 51 51);
}

.high {
  background: #734b00;
}

.medium {
  background: #3f3f75;
}

.low {
  background: #003e00;
}

.standard {
  background: rgb(62, 77, 100);
}

.notes_list__note > span {
  margin-left: 0.5rem;
}

.notes_list__note_calendarLabel {
  cursor: pointer;
  font-size: 1.5rem;
  color: rgb(20, 13, 61);
  transition: 0.5s;
}

.notes_list__note_calendarLabel_inactive {
  cursor: pointer;
  font-size: 1.5rem;
  color: gray;
  transition: 0.5s;
}

.notes_list__note_calendarLabel_active {
  scale: 1.5;
  color: rgb(146 134 215);
}

.notes_list__note_calendarLabel:hover {
  scale: 1.5;
}

.notes_list__note_checkLabel {
  margin-left: auto;
  font-size: 2rem;
  color: gray;
  transition: 0.5s;
}

.notes_list__note_checkLabel:hover {
  cursor: pointer;
  color: rgb(12, 122, 12);
  scale: 1.4;
}

.notes_list__note_crossLabel {
  font-size: 2rem;
  color: gray;
  transition: 0.5s;
}

.notes_list__note_crossLabel:hover {
  cursor: pointer;
  color: rgb(85, 23, 19);
  scale: 1.4;
}
