.note__menu {
  visibility: hidden;
  position: absolute;
  right: -142px;
  top: -2px;
  border: 2px solid gray;
  border-radius: 0.3rem;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.note__menu_active {
  visibility: visible;
}

.note__menu > span {
  font-weight: bold;
}

.note__menu__prior_list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

.note__menu__prior_list > li:hover {
  cursor: pointer;
  text-decoration: underline;
}
