.app__notes_actions {
  margin-top: 1rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.app__notes_actions__deleteAll,
.app__notes_actions__deleteCompleted {
  padding: 0.7rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app__notes_actions__deleteAll:hover,
.app__notes_actions__deleteCompleted:hover {
  cursor: pointer;
}
